@font-face {
  font-family: 'VT323';
  font-style: normal;
  font-weight: 400;
  src: local('VT323 Regular'), local('VT323-Regular'), url(/pit-trader/static/media/monoton.e3552630.woff2) format('woff2');
}

body {
  margin: 0;
  padding: 0;
  font-family: 'VT323';
  -webkit-perspective: 1000px;
          perspective: 1000px;
  -webkit-perspective-origin: bottom center;
          perspective-origin: bottom center;
}

html, body, #root {
  height: 100%;
}
@-webkit-keyframes hands-waggle {
  from {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  10% {
    -webkit-transform: translateZ(100px) translateX(0%);
            transform: translateZ(100px) translateX(0%);
  }
  20% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  30% {
    -webkit-transform: translateX(-10%);
            transform: translateX(-10%);
  }
  40% {
    -webkit-transform: translateZ(-100px) translateX(0%);
            transform: translateZ(-100px) translateX(0%);
  }
  50% {
    -webkit-transform: translateZ(-100px) translateX(10%);
            transform: translateZ(-100px) translateX(10%);
  }
  60% {
    -webkit-transform: translateZ(-100px) translateX(0%);
            transform: translateZ(-100px) translateX(0%);
  }
  70% {
    -webkit-transform: translateZ(-100px) translateX(-10%);
            transform: translateZ(-100px) translateX(-10%);
  }
  80% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  90% {
    -webkit-transform: translateX(10%);
            transform: translateX(10%);
  }
  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes hands-waggle {
  from {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  10% {
    -webkit-transform: translateZ(100px) translateX(0%);
            transform: translateZ(100px) translateX(0%);
  }
  20% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  30% {
    -webkit-transform: translateX(-10%);
            transform: translateX(-10%);
  }
  40% {
    -webkit-transform: translateZ(-100px) translateX(0%);
            transform: translateZ(-100px) translateX(0%);
  }
  50% {
    -webkit-transform: translateZ(-100px) translateX(10%);
            transform: translateZ(-100px) translateX(10%);
  }
  60% {
    -webkit-transform: translateZ(-100px) translateX(0%);
            transform: translateZ(-100px) translateX(0%);
  }
  70% {
    -webkit-transform: translateZ(-100px) translateX(-10%);
            transform: translateZ(-100px) translateX(-10%);
  }
  80% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
  90% {
    -webkit-transform: translateX(10%);
            transform: translateX(10%);
  }
  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

.hands {
  height: 100%;
  width: 100%;
  position: relative;
  will-change: transform;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-name: hands-waggle;
          animation-name: hands-waggle;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
}

@-webkit-keyframes skipping-jumper {
  from {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
  }
  49% {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
  }
  50% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
  to {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes skipping-jumper {
  from {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
  }
  49% {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
